let getRarityDisplay rarityRanking =
    match rarityRanking with
    | _ when rarityRanking < 0.0 -> ofOption None
    | _ when rarityRanking < 0.01 ->
        div [ClassName "rarity ranking-1 small-shadow mode-aware-border2"] [div [ClassName "mode-aware-border2"] [str "Extremely Rare"]; div [ClassName "mode-aware-border2 mode-aware-text"] [span [ClassName "emoji"] [str ""]; str "Top 1%"]]
    | _ when rarityRanking < 0.05 ->
        div [ClassName "rarity ranking-5 small-shadow mode-aware-border2"] [div [ClassName "mode-aware-border2 mode-aware-gradient-back"] [str "Very Rare"]; div [ClassName "mode-aware-border2 mode-aware-gradient-back mode-aware-text"] [span [ClassName "emoji"] [str ""]; str "Top 5%"]]
    | _ when rarityRanking < 0.1 ->
        div [ClassName "rarity ranking-10 small-shadow mode-aware-border2"] [div [ClassName "mode-aware-border2 mode-aware-gradient-back"] [str "Rarity"]; div [ClassName "mode-aware-border2 mode-aware-gradient-back mode-aware-text"] [span [ClassName "emoji"] [str ""]; str "Top 10%"]]
    | _ when rarityRanking < 0.2 ->
        div [ClassName "rarity ranking-20 small-shadow mode-aware-border2"] [div [ClassName "mode-aware-border2 mode-aware-gradient-back"] [str "Rarity"]; div [ClassName "mode-aware-border2 mode-aware-gradient-back mode-aware-text"] [str "Top 20%"]]
    | _ when rarityRanking < 0.3 ->
        div [ClassName "rarity ranking-30 small-shadow mode-aware-border2"] [div [ClassName "mode-aware-border2 mode-aware-gradient-back"] [str "Rarity"]; div [ClassName "mode-aware-border2 mode-aware-gradient-back mode-aware-text"] [str "Top 30%"]]
    | _ -> ofOption None

let profileImage (profileNft :NFT option) treeAccount =
    match profileNft with
    | None -> jazziconForAddress 100 treeAccount
    | Some nft ->
        div [ClassName "image-outer"]
            [div [ClassName "image"; Style [BackgroundImage ("url(" + nft.display.url + ")")]] []]

let themeStyles (colors: string[]) (darkMode: bool) =
    let color1 = colors.[0]
    let color2 = colors |> Array.last
    let gradientFill = style [] [str (sprintf ".mode-aware-gradient { background: %s }" (getSimpleGradient colors))]
    let gradientFillHover = style [] [str (sprintf ".mode-aware-gradient-hover { background: %s; transition: background 500ms linear } .mode-aware-gradient-hover:hover { background: %s; transition: background 500ms linear }" (getSimpleGradient colors) (getSimpleGradient (colors |> Array.map (ColorUtils.lighten 0.2))))]
    let gradientBack = getGradientBackgroundStyle colors darkMode "mode-aware-gradient-back"
    let borders =
        let (h, s, _) = color1 |> ColorUtils.fromHexStr |> ColorUtils.fromRgb
        let r, g, b = ColorUtils.toRgb (h, s * 0.8, if darkMode then 0.85 else 0.15)
        let r2, g2, b2 = ColorUtils.toRgb (h, s * 0.2, if darkMode then 0.2 else 0.9)
        style [] [str (sprintf ".mode-aware-border { border-color: rgb(%i, %i, %i, 0.2) !important } .mode-aware-border2, .mode-aware-border2::before { border-color: rgb(%i, %i, %i) !important } .mode-aware-nftback { background-color: rgb(%i, %i, %i) !important }" r g b r2 g2 b2 r2 g2 b2)]
    let text =
        let (h, s, _) = color1 |> ColorUtils.fromHexStr |> ColorUtils.fromRgb
        let r, g, b = ColorUtils.toRgb (h, s * 0.7, if darkMode then 0.85 else 0.25)
        let r2, g2, b2 = ColorUtils.toRgb (h, s * 0.15, if darkMode then 0.45 else 0.55)
        let r3, g3, b3 = ColorUtils.toRgb(h, s * 0.24, if darkMode then 0.65 else 0.35)
        let (h2, s2, _) = colors |> Array.last |> ColorUtils.fromHexStr |> ColorUtils.fromRgb
        let r4, g4, b4 = ColorUtils.toRgb(h2, s2, if darkMode then 0.75 else 0.35)
        let r5, g5, b5 = ColorUtils.toRgb(h2, s2, if darkMode then 0.65 else 0.45)
        style [] [str (sprintf ".mode-aware-text { color: rgb(%i, %i, %i) !important } .mode-aware-text2 { color: rgb(%i, %i, %i) !important } strong { color: rgb(%i, %i, %i) !important  } a { color: rgb(%i, %i, %i) } a:hover { color: rgb(%i, %i, %i) }" r g b r2 g2 b2 r3 g3 b3 r4 g4 b4 r5 g5 b5)]
    let background =
        let tcolor = if ColorUtils.isSlightlyLight [color1] then "black" else "white"
        style [] [str (sprintf ".mode-aware-background { background-color: %s; color: %s }" color1 tcolor)]
    let shadows =
        let (h, s, _) = color2 |> ColorUtils.fromHexStr |> ColorUtils.fromRgb
        let r, g, b = ColorUtils.toRgb (h, s * 0.5, if darkMode then 0.05 else 0.25)
        let a = if darkMode then 1.0 else 0.3
        style [] [str (sprintf ".small-shadow { box-shadow: 0px 0px 12px 0px rgba(%i, %i, %i, %f) !important } .large-shadow { box-shadow: 0px 0px 36px 0px rgba(%i, %i, %i, %f) !important }" r g b a r g b a)]
    div []
        [gradientFill
         gradientFillHover
         gradientBack
         borders
         text
         background
         shadows]

