type GradientButtonProps = { colors: string[]; name: string; selected: bool; onSelect: unit -> unit; disabled: bool }

let GradientButton =
    FunctionComponent.Of ((fun (props :GradientButtonProps) ->
        let gradient = getSimpleGradient props.colors
        let color = if ColorUtils.isLight (props.colors :> string seq) then "black" else "white"
        div [classList ["disabled", props.disabled];OnClick (props.onSelect << ignore);Style [Background gradient; Color color]] [str props.name]
    ), "GradientButton", equalsButFunctions, fun props -> props.name)

type ThemeSelectorProps = { colors: (string[] * string)[]; selectedColor: int; darkMode: bool; disabled: bool; onSelect: int -> unit; onSelectDarkMode: bool -> unit }

let ThemeSelector =
    FunctionComponent.Of ((fun (props :ThemeSelectorProps) ->
        div [ClassName "theme-selector"]
            [div [ClassName "gradient-selector"]
                    (props.colors
                        |> Array.mapi (fun i (cs, c) ->
                            GradientButton { disabled = props.disabled; colors = cs; name = c; selected = i = props.selectedColor; onSelect = (fun _ -> props.onSelect i) })
