             div [ClassName "mode"]
                [div [classList ["disabled", props.disabled]; OnClick (fun _ -> props.onSelectDarkMode true)] [mdiIcon "weather-night"]
                 div [classList ["disabled", props.disabled]; OnClick (fun _ -> props.onSelectDarkMode false)] [mdiIcon "weather-sunny"]]]
    ), "ThemeSelector", equalsButFunctions)

