type NftDetailsModalProps =
    { singlePageMode: bool; nft: NFT; collection: NFTCollection; likeCount: int; onClose: unit -> unit }

let NftDetailsModal =
    FunctionComponent.Of ((fun (props :NftDetailsModalProps) ->
        let nftDetails = Feliz.React.useDeferred (Server.getNFTDetails props.nft.token_address props.nft.token_id, [||])
        let title =
            if isNullOrEmpty props.nft.title then
                if not <| isNullOrEmpty props.nft.token_id then
                    try
                        "#" + System.Int32.Parse(props.nft.token_id, System.Globalization.NumberStyles.HexNumber).ToString()
                    with
                    | _ -> ""
                else ""
            else ""
        window?nftDetails <- nftDetails
        let attributes =
            match nftDetails with
            | Deferred.Resolved (Some { attributes = attributes }) when attributes.Length > 0 ->
                NftAttributeList { attributes = attributes }
            | _ -> ofOption None
        div [classBaseList "rmodal" ["single-page", props.singlePageMode]]
            [div [ClassName "rmodal-back mode-aware-gradient-back"]
                [a [OnClick (props.onClose << ignore); ClassName "mode-aware-text"] [mdiIcon "chevron-left"; str "Back"]]
             div [ClassName "rmodal-close"]
                [button [OnClick (props.onClose << ignore)] [mdiIconLrg "close"]]
             div [ClassName "rmodal-left"]
                [div [ClassName "rmodal-media"] [NftMediaFull props.nft.display]]
             div [ClassName "rmodal-right mode-aware-gradient-back"]
                [h1 [] [str props.collection.name]
                 h2 [ClassName "mode-aware-border"] [str title]
                 div [] [attributes]]]
            ), "NftDetailsModal", equalsButFunctions)

