// Transaction Modal
// ----------
type DialogModalProps =
    { active: bool; txResult: DappTxResult<TransactionMsg>; accountProfileInfo: ProfileInfo option; account: string; ens: string option; url: string option }

let DialogModal =
    FunctionComponent.Of ((fun (props :DialogModalProps) ->
        let onCopy url =
            Browser.Dom.window?navigator?clipboard?writeText(url)
        let msg =
            match props.txResult with
            | Response (UpdateProfile(_), _) ->
                "Saving your page..."
            | Confirmed (UpdateProfile(_), _) ->
                "Your new page has been saved to the blockchain."
            | _ -> ""
        let image =
            match props.accountProfileInfo with
            | Some { image = image } when not <| isNullOrEmpty image ->
                img [ClassName "profile-image mode-aware-border"; Src image]
            | _ -> jazziconForAddress 48 props.account
        let name =
            match props.accountProfileInfo with
            | Some info -> info.displayName
            | _ -> niceAddress props.account
        let blockExplorerUrl =
            match props.txResult with
            | Response (_, response) -> Config.getBlockExplorerUrl response.hash |> Some
            | Confirmed (_, confirmed) -> Config.getBlockExplorerUrl confirmed.transactionHash |> Some
            | _ -> None
        let blockExpLink =
            blockExplorerUrl |> Option.map (fun url -> p [ClassName "blockexp-link"] [a [Href url; Target "_blank"] [str "View on Block Explorer"]])
        let bottom =
            match props.txResult with
            | Response _ -> div [ClassName "lds-dual-ring"] []
            | Confirmed _ ->
                let url =
                    match props.ens, props.url with
                    | Some ens, _ -> Config.siteUrl + "/" + ens
                    | _, Some url -> Config.siteUrl + "/" + url
                    | _ -> Config.siteUrl + "/" + props.account
                div [ClassName "save-result"]
                    [p [] [str "Your page is available at "; a [Href url] [str url]; str " "; a [OnClick (fun _ -> onCopy url);ClassName "button is-small mode-aware-button mode-aware-border"] [mdiIcon "content-copy"]]
                     a [Href ("/edit/" + props.account); ClassName "button mode-aware-button mode-aware-border"] [str "Close"]]
                //a [ClassName "button"; Href ("/" + props.account)] [str "View Tree"]
            | _ -> ofOption None
        PageModal
            { active = props.active
              children =
                [div [ClassName "saving-modal mode-aware-border mode-aware-gradient-back"]
                    [h1 [ClassName "mode-aware-text"] [str msg]
                     div [ClassName "profile-info"]
                        [image
                         h1 [ClassName "mode-aware-text"] [str name]]
                     ofOption blockExpLink
                     bottom]]}
    ), "DialogModal", equalsButFunctions)

