// Edit Bar
// --------
type EditBarProps =
    { saveDisabled: bool; treeAccount: string; treeChainId: int; onSave: unit -> unit }

let EditBar =
    FunctionComponent.Of ((fun (props :EditBarProps) ->
        let web3State = useWeb3React()
        let saveDisabled = props.saveDisabled || (web3State.account <> Some props.treeAccount) || web3State.chainId <> Some props.treeChainId
        nav [ClassName "navbar edit is-fixed-top"]
            [div [ClassName "navbar-brand"]
                [div [ClassName "navbar-item"]
                   (match web3State with
                    | { account = Some account; chainId = Some chainId; active = true } when account = props.treeAccount && chainId = props.treeChainId ->
                        [span [ClassName "icon"] [i [ClassName "mdi mdi-cog-outline"] []]; span [] [str "Editing"]]
                    | { active = false } ->
                        [button [ClassName "button connect is-primary"; OnClick (fun _ -> web3State.activate(Injected))] [str "Connect to Edit"]]
                    | _ ->
                        let errTxt =
                            match web3State with
                            | { account = Some account } when account <> props.treeAccount -> "Wrong account"
                            | { chainId = Some chainId } when chainId <> props.treeChainId -> "Wrong chain"
                            | _ -> "There is a problem with your web3 provider"
                        [span [ClassName "icon has-text-danger"] [i [ClassName "mdi mdi-alert-octagon"] []]; span [ClassName "has-text-danger"] [str errTxt]])
                 ]
             div [ClassName "navbar-item controls"]
                [button [OnClick (props.onSave << ignore);ClassName "button save-button"; Disabled saveDisabled]
                    [span [ClassName "icon"] [i [ClassName "mdi mdi-content-save-outline"] []]
                     span [] [str "Save"]]]]
        ), "EditBar", equalsButFunctions)

// Link Tree Edit Link
// -------
type LinkTreeEditLinkProps =
    { link: EditLinkState
      isLight: bool
      isDark: bool
      canEdit: bool
      canMoveUp: bool
      canMoveDown: bool
      onEdit: unit -> unit
      onAccept: (string * string) -> unit
      onRevert: unit -> unit
      onUp: unit -> unit
      onDown: unit -> unit
      onDelete: unit -> unit }

let LinkTreeEditLink =
    FunctionComponent.Of ((fun (props :LinkTreeEditLinkProps) ->
        let editing = Hooks.useState false
        let name = Hooks.useState props.link.name
        let url = Hooks.useState props.link.url
        let hasChanges = name.current <> props.link.name || url.current <> props.link.url
        let valid = Validate.linkUrl url.current && Validate.linkName name.current
        let onCancel _ =
            name.update props.link.name
            url.update props.link.url
            editing.update false
            props.onRevert()
            if props.link.isNew then props.onDelete()
        let isEditing = editing.current || props.link.isNew
        let control =
            match isEditing with
            | false -> []
            | true ->
                let nameValid = hasChanges && Validate.linkName name.current
                let nameInvalid = hasChanges && not <| Validate.linkName name.current
                let urlValid = hasChanges && Validate.linkUrl url.current
                let urlInvalid = hasChanges && not <| Validate.linkUrl url.current
                [input [classBaseList "input link-name" ["valid", nameValid; "invalid", nameInvalid]; Type "text"; Placeholder "Title"; DefaultValue name.current; OnChange (fun e -> name.update (e.target?value :> string))]
                 input [classBaseList "input link-name" ["valid", urlValid; "invalid", urlInvalid]; Type "text"; Placeholder "Url"; DefaultValue url.current; OnChange (fun e -> url.update (e.target?value :> string))]]
        let accept _ =
            editing.update false
            props.onAccept (name.current, url.current)
        let controlButtons =
            match editing.current, props.link.isNew with
            | true, _ | _, true ->
                [button [ClassName "control-cancel"; OnClick onCancel] [span [ClassName "icon"] [i [ClassName "mdi mdi-arrow-u-left-top-bold mdi-24px"] []]]
                 button [ClassName "control-accept"; OnClick accept; Disabled <| not valid] [span [ClassName "icon"] [i [ClassName "mdi mdi-check-outline mdi-24px"] []]]
                ]
            | _ ->
                [if props.canEdit then button [ClassName "control-edit"; OnClick (fun _ -> props.onEdit(); editing.update true; )] [span [ClassName "icon"] [i [ClassName "mdi mdi-square-edit-outline mdi-24px"] []]] else ofOption None
                 if props.canMoveUp then button [ClassName "control-up"; OnClick (fun _ -> props.onUp())] [span [ClassName "icon"] [i [ClassName "mdi mdi-arrow-up-box mdi-24px"] []]] else ofOption None
                 if props.canMoveDown then button [ClassName "control-down"; OnClick (fun _ -> props.onDown())] [span [ClassName "icon"] [i [ClassName "mdi mdi-arrow-down-box mdi-24px"] []]] else ofOption None
                 button [ClassName "control-delete"; OnClick (fun _ -> props.onDelete())] [span [ClassName "icon"] [i [ClassName "mdi mdi-delete mdi-24px"] []]]]
        div [classBaseList "field edit-link mode-aware-gradient" ["editing", isEditing; "is-dark", props.isDark; "is-light", props.isLight]]
            [div [ClassName "name"] [str props.link.name]
             div [ClassName "control"] control
             div [ClassName "control-buttons"]
                controlButtons]
    ), "LinkTreeEditLink", equalsButFunctions, fun props -> props.link.id + props.link.index.ToString())

// Link Tree Edit
// --------
type LinkTreeEditProps =
    { treeAccount: string
      ens: string option
      hide: bool
      state: EditState option
      onLinkAccept: (string * string * string) -> unit
      onLinkUp: string -> unit
      onLinkDown: string -> unit
      onLinkDelete: string -> unit
      onNameChange: string -> unit
      onBioChange: string -> unit
      onLinkNew: unit -> unit
      onSelectColor: string[] -> unit
      onSelectDarkMode: bool -> unit
      isColorLight: bool
      isColorDark : bool
      disabled: bool }

let LinkTreeEdit =
    FunctionComponent.Of ((fun (props :LinkTreeEditProps) ->
        let editing = Hooks.useState None
        match props.state with
        | None -> bigLoading
        | Some state ->
            let canAdd =
                match editing.current with
                | None when not (state.links |> Array.exists (fun l -> l.isNew)) ->
                    true
                | _ -> false
            div [ClassName "tree edit"; Style [if props.hide then yield Display DisplayOptions.None]]
                [themeStyles state.colors state.darkMode
                 div [ClassName "profile-info"]
                    [profileImage state.profileNft props.treeAccount
                     EditInput { disabled = props.disabled; value = state.name; name = "name"; placeholder = "Name"; onChange = props.onNameChange }
                     EditTextArea { disabled = props.disabled; value = state.bio; name = "bio"; placeholder = "Bio"; onChange = props.onBioChange }]
                 ofArray (state.links
                    |> Array.mapi (fun i link ->
                        let canEdit =
                            match editing.current with
                            | Some id when id = link.id -> true
                            | None when not (state.links |> Array.exists (fun l -> l.isNew)) -> true
                            | _ when link.isNew -> true
                            | _ -> false
                        LinkTreeEditLink
                            { link = link
                              isLight = props.isColorLight
                              isDark = props.isColorDark
                              canEdit = canEdit
                              canMoveDown = i < state.links.Length - 1
                              canMoveUp = i > 0
                              onEdit = fun _ -> editing.update (Some link.id)
                              onAccept = fun (name, url) -> props.onLinkAccept (link.id, name, url); editing.update None
                              onUp = fun _ -> props.onLinkUp link.id
                              onDown = fun _ -> props.onLinkDown link.id
                              onDelete = fun _ -> props.onLinkDelete link.id
                              onRevert = fun _ -> editing.update None }))
                 button [Disabled (not canAdd || props.disabled);OnClick (props.onLinkNew << ignore);ClassName "add-link button"]
                    [span [ClassName "icon"] [i [ClassName "mdi mdi-link-plus"] []]; span [] [str "Add Link"]]
                 ThemeSelector { disabled = props.disabled; colors = ColorUtils.gradientArray; selectedColor = 0; darkMode = false; onSelect = (fun i -> props.onSelectColor (fst ColorUtils.gradientArray.[i])); onSelectDarkMode = props.onSelectDarkMode }
                 ]
    ), "LinkTreeEdit", equalsButFunctions)

