// ---------
type LinkTreeLinkProps =
    { name: string; url: string; index: int; showBorder: bool; darkText: bool }

let LinkTreeLink =
    FunctionComponent.Of ((fun (props :LinkTreeLinkProps) ->
        let icon =
            match props.url with
            | url when url.Contains("instagram.com") -> i [ClassName "mdi mdi-instagram"] []
            | url when url.Contains("twitter.com") -> i [ClassName "mdi mdi-twitter"] []
            | url when url.Contains("facebook.com") -> i [ClassName "mdi mdi-facebook"] []
            | url when url.Contains("discord.com") -> i [ClassName "mdi mdi-discord"] []
            | _ -> ofOption None
        let style =
            [if props.showBorder then
                yield BorderStyle "solid"
                yield BorderWidth "1px"
             if props.darkText then
                yield Color "black"]
        a [classBaseList "button mode-aware-gradient-hover" ["mode-aware-border", props.showBorder]; Href props.url; Style style] [icon; str props.name]
        ), "LinkTreeLink", equalsButFunctions, fun props -> props.index.ToString())

// Link Tree
// ---------
type LinkTreeViewProps =
    { treeAccount: string; ens: string option; tree: TreeData; profileNft: NFT option; hide: bool }

let LinkTreeView =
    FunctionComponent.Of ((fun (props :LinkTreeViewProps) ->
        // set html class
        let tree = props.tree
        let profileNft = props.profileNft
        let profileImage = profileImage profileNft props.treeAccount
        let name =
            if isNullOrEmpty tree.name then
                match props.ens with
                | Some ens when not <| isNullOrEmpty ens -> ens
                | _ -> niceAddress props.treeAccount
            else tree.name
        let showBorder =
            (props.tree.colors |> ColorUtils.isDark && tree.darkMode) || (props.tree.colors |> ColorUtils.isLight && (not tree.darkMode))
        let darkText = props.tree.colors |> ColorUtils.isLight
        div [ClassName "tree view"]
           [yield themeStyles tree.colors tree.darkMode
            // header
            if not props.hide then
                yield div [ClassName "profile-info"]
                   [// profile image
                    profileImage
                    // name
                    h1 [ClassName "mode-aware-text"] [str name]
                    // bio
                    p [] [str tree.bio]]
                // links
                yield div [ClassName "links"] (tree.links |> Array.mapi (fun i link -> LinkTreeLink { name = link.name; url = link.url; index = i; showBorder = showBorder; darkText = darkText }) |> Array.toList)
            ]
            ), "LinkTreeView", equalsButFunctions, fun props -> props.treeAccount)

