type NftCollectionBadgeProps =
    { n: int; imageUrl: string; collectionIndex: int; onClick: unit -> unit }

let NftCollectionBadge =
    FunctionComponent.Of ((fun (props :NftCollectionBadgeProps) ->
        let sub =
            match props.n with
            | 0 -> ofOption None
            | n ->
                div [ClassName "mode-aware-background mode-aware-border2 small-shadow"] [span [] [str (sprintf "%i" n)]]
        a [OnClick (props.onClick << ignore); ClassName "nft-collection-badge"]
