let NftMedia =
    FunctionComponent.Of ((fun (props :NftMediaProps) ->
        let videoLoading = Hooks.useState false
        match props.nft.display.display, props.videoProps with
        | "video", Some videoProps ->
            if props.nft.display.hasPreview then
                let src =
                    match props.nft.display.hasW388 with
                    | true -> props.nft.display.url + "_preview_w388"
                    | _ ->  props.nft.display.url + "_preview"
                div [ClassName "video-preview"]
                    [img [OnClick (props.onClick << ignore); classBaseList "mode-aware-border2 mode-aware-nftback" ["hidden", props.hidden]; Src src]
                     (match videoProps.playing with
                      | true ->
                        video [OnEnded (videoProps.onEnded << ignore); OnPause (videoProps.onEnded << ignore); OnPlay (fun _ -> videoLoading.update false; videoProps.onPlay()); classBaseList "mode-aware-border2" ["started", videoProps.started]; AutoPlay true; Muted isMobile]
                            [source [Src props.nft.display.url; Type (getMimeType props.nft.display.format)]]
                      | _ -> ofOption None)
                     if videoLoading.current then
                        div [ClassName "video-loading"] [div [ClassName "lds-dual-ring"] []]
                     else ofOption None
                     div [classBaseList "video-controls" ["playing", videoProps.started]]
                        [div [ClassName "video-controls-inner"]
                            [a [ClassName "video-stop"; OnClick (videoProps.onEnded << ignore)] [mdiIcon "stop"]
                             a [ClassName "video-play"; OnClick (fun _ -> videoLoading.update true; videoProps.playVideo())] [mdiIcon "play"]]]]
            else
                video [OnClick (props.onClick << ignore); classBaseList "mode-aware-border2" ["hidden", props.hidden]; AutoPlay false; Controls true]
                    [source [Src props.nft.display.url; Type (getMimeType props.nft.display.format)]]
        | "model", _ ->
            modelViewer [Src props.nft.display.url; HTMLAttr.Custom ("bounds", "tight"); HTMLAttr.Custom("enable-pan", true); HTMLAttr.Custom("auto-rotate", "true"); HTMLAttr.Custom("shadow-intensity", 2); HTMLAttr.Custom("shadow-softness", 0.65); HTMLAttr.Custom ("ar-modes", "webxr scene-viewer quick-look"); HTMLAttr.Custom ("exposure", 1); HTMLAttr.Custom("environment-image", "neutral")]
                [div [ClassName "model-viewer-load"; HTMLAttr.Custom ("slot", "poster")] [bigLoading]]
        | "text", _ ->
            iframe [Src props.nft.display.url; HTMLAttr.Width "340"; HTMLAttr.Height "550"] []
        | _ ->
            let src =
                match props.nft.display.hasW388 with
                | true -> props.nft.display.url + "_w388"
                | _ -> props.nft.display.url
            img [OnClick (props.onClick << ignore); HTMLAttr.Custom("decoding", "async"); HTMLAttr.Custom("loading", "lazy"); classBaseList "mode-aware-border2 mode-aware-nftback large-shadow" ["hidden", props.hidden]; Src src]
    ), "NftMedia", equalsButFunctions, fun props -> props.nft.token_address + props.nft.token_id)

