type NftGalleryNftProps =
    { nft: NFTResult
      hidden: bool
      onNftClick: (NFTResult * NFTCollection) -> unit
      canMakeProfile: bool
      collection: NFTCollection
      videoProps: NftMediaVideoProps option
      mode: Mode
      disabled: bool
      onToggleVisible: unit -> unit
      onMakeProfile: unit -> unit }

let NftGalleryNft =
    FunctionComponent.Of ((fun (props :NftGalleryNftProps) ->
        let controls =
            match props.mode with
            | View -> ofOption None
            | Edit ->
                let showHideInner =
                    match props.hidden with
                    | false -> [mdiIcon "eye-off"; str " Hide"]
                    | true -> [mdiIcon "eye"; str " Show"]
                let makeProfile =
                    match props.nft.nft.display.display with
                    | "image" ->
                        button [OnClick (props.onMakeProfile << ignore);ClassName "button"; Disabled (not props.canMakeProfile || props.disabled)]
                            [str "Make Profile"]
                    | _ -> ofOption None
                div [ClassName "edit-nft"]
                    [button [Disabled props.disabled; ClassName "button"; OnClick (props.onToggleVisible << ignore)] showHideInner
                     makeProfile]
        let title =
            if isNullOrEmpty props.nft.nft.title then
                if not <| isNullOrEmpty props.nft.nft.token_id then
                    try
                        "#" + System.Int32.Parse(props.nft.nft.token_id, System.Globalization.NumberStyles.HexNumber).ToString()
                    with
                    | _ -> ""
                else ""
            else ""

        div [ClassName "nft"]
            [// Media
             NftMedia
                { nft = props.nft.nft
                  hidden = props.hidden
                  videoProps = props.videoProps
                  onClick = fun () -> props.onNftClick (props.nft, props.collection) }
             // Controls if edit mode
             controls
             let desc =
                if isNullOrEmpty props.collection.description && not <| isNullOrEmpty props.nft.nft.description then
                    p [ClassName "nft-desc"] [str props.nft.nft.description]
                else ofOption None
            // Card
             div [ClassName "nft-card mode-aware-border"]
                [p [ClassName "nft-title"] [str title]
                 p [ClassName "nft-media"] (getMediaElement props.nft.nft.display)
                 getRarityDisplay props.nft.rarityRanking
                 desc]
             ]
    ), "NftGalleryNft", equalsButFunctions, fun props -> props.nft.nft.token_address + props.nft.nft.token_id)

