type NftGalleryCollectionProps =
    { collection: NFTCollectionResult
      scrollToRef: IRefValue<Browser.Types.Element option> option
      mode: Mode
      disabled: bool
      displayUntil: int
      hidden: Set<string * string>
      profileNft: (string * string) option
      videoPlaying: (string * string * bool) option
      onNftClick: (NFTResult * NFTCollection) -> unit
      onEnded: (string * string) -> unit
