let NftGalleryCollection =
    FunctionComponent.Of ((fun (props :NftGalleryCollectionProps) ->
        let collectionHeading =
            if isNullOrEmpty props.collection.collection.bannerUrl || isNullOrEmpty props.collection.collection.imageUrl then
                div [ClassName "collection-heading"]
                    [h1 [] [str props.collection.collection.name]]
            else
                div [ClassName "collection-heading"]
                    [div [ClassName "banner-image mode-aware-border2 large-shadow"; Style [BackgroundImage (sprintf "url(%s)" props.collection.collection.bannerUrl)]] []
                     div [ClassName "collection-image mode-aware-border2"; Style [BackgroundImage (sprintf "url(%s)" props.collection.collection.imageUrl)]] []
                     h1 [] [str props.collection.collection.name]]
        let collectionDesc =
            if isNullOrEmpty props.collection.collection.description then
                ofOption None
            else
                div [ClassName "collection-description"]
                    [h2 [] [markdown [str props.collection.collection.description]]]
        div (match props.scrollToRef with | None -> [ClassName "collection"] | Some ref -> [ClassName "collection"; RefValue ref])
            [div [ClassName "collection-spacer mode-aware-border"] [str " "]
             collectionHeading
             collectionDesc
             div [ClassName "nfts"]
                (props.collection.nfts
                    |> Array.map (fun nft ->
                        let videoProps =
                            match nft.nft.display.display with
                            | "video" ->
                                { playing =
                                    match props.videoPlaying with
                                    | Some (tokenAddress, tokenId, _) when tokenAddress = nft.nft.token_address && tokenId = nft.nft.token_id ->
                                        true
                                    | _ -> false
                                  started =
                                    match props.videoPlaying with
                                    | Some (tokenAddress, tokenId, true) when tokenAddress = nft.nft.token_address && tokenId = nft.nft.token_id ->
                                        true
                                    | _ -> false
                                  onEnded = fun _ -> props.onEnded (nft.nft.token_address, nft.nft.token_id)
                                  onPlay = fun _ -> props.onPlay (nft.nft.token_address, nft.nft.token_id)
                                  playVideo = fun _ -> props.playVideo (nft.nft.token_address, nft.nft.token_id) } |> Some
                            | _ -> None
                        match props.mode with
                        | View when props.hidden |> Set.contains (nft.nft.token_address, nft.nft.token_id) -> ofOption None
                        | _ when nft.index >= props.displayUntil -> ofOption None
                        | _ ->
                            NftGalleryNft
                                { nft = nft
                                  onNftClick = props.onNftClick
                                  disabled = props.disabled
                                  onToggleVisible = fun _ -> props.onToggleVisible (nft.nft.token_address, nft.nft.token_id)
                                  onMakeProfile = fun _ -> props.onMakeProfile (nft.nft.token_address, nft.nft.token_id)
                                  hidden = props.hidden |> Set.contains (nft.nft.token_address, nft.nft.token_id)
                                  videoProps = videoProps
                                  collection = props.collection.collection
                                  canMakeProfile = props.profileNft <> Some (nft.nft.token_address, nft.nft.token_id)
                                  mode = props.mode}))]
    ), "NftGalleryCollection", equalsButFunctions, fun props -> props.collection.collection.token_address)

// NFT Gallery
// ----------
type NftGalleryProps =
    { nftAccount: string
      hidden: Set<string * string>
      mode: Mode
      disabled: bool
      onGoToCollection: int * int -> unit
      profileNft: (string * string) option
      onToggleVisible: string * string -> unit
      onMakeProfile: NFT -> unit }

let NftGallery =
    FunctionComponent.Of ((fun (props :NftGalleryProps) ->
        // hooks
        let nfts = Feliz.React.useDeferred (Server.getNFTs props.nftAccount, [||])
        window?nfts <- nfts
        let videoPlaying = Hooks.useState<(string * string * bool) option> None
        let displayUntil, displayUntilUpdater = Feliz.React.useStateWithUpdater Config.nftPageSize
        let displayFrom = Hooks.useState 0
        let firstColRef, scrollToFirstCol = useScrollTo()
        let nftDetails = Hooks.useState None
        let onNftClick =
            Feliz.React.useCallbackRef(fun (nft :NFTResult * NFTCollection) -> nftDetails.update(Some nft))
        let onModalClose = Feliz.React.useCallbackRef(fun () -> nftDetails.update(None))
        Hooks.useEffect(fun () ->
            match nftDetails.current with
            | None -> document.getElementsByTagName("html").[0].classList.remove("has-modal")
            | Some _ -> document.getElementsByTagName("html").[0].classList.add("has-modal"))
        // infinite scrolling event
        Hooks.useEffectDisposable(fun () ->
            let handleScroll =
                fun _ ->
                    if ((window.innerHeight + window.pageYOffset + 150.0) >= document.body.offsetHeight) then
                        displayUntilUpdater (fun x -> x + Config.nftPageSize)
            window.addEventListener("scroll", handleScroll)
            { new System.IDisposable with
                member _.Dispose() = window.removeEventListener("scroll", handleScroll) })
        let goToCollection =
            Feliz.React.useCallbackRef (fun (i :int) ->
                displayFrom.update(i)
                match nfts with
                | Deferred.Resolved nfts ->
                    let collections =
                        nfts.results |> Array.mapi (fun i c -> i, c) |> Array.choose (fun (i, c) ->
                            let n = c.nfts |> Array.filter (fun n -> not (props.hidden |> Set.contains (n.nft.token_address, n.nft.token_id))) |> Array.length
                            if n > 0 then Some (c.collection, i, c.nfts.Length) else None)
                    let firstColIndex = collections |> Array.tryHead |> Option.map (fun (_, i, _) -> i) |> Option.defaultValue 0
                    displayUntilUpdater (fun _ -> nfts.results.[i].nfts.[0].index + Config.nftPageSize)
                    if i > firstColIndex then
                        scrollToFirstCol ()
                    else
                        // scroll to top
                        window.scrollTo(0.0, 0.0)
                    props.onGoToCollection (firstColIndex, i)
                | _ -> ())
        match nfts with
        | Deferred.InProgress | Deferred.HasNotStartedYet -> bigLoading
        | Deferred.Failed e -> errorView
        | Deferred.Resolved nfts ->
            let collections =
                nfts.results |> Array.mapi (fun i c -> i, c) |> Array.choose (fun (i, c) ->
                    let n = c.nfts |> Array.filter (fun n -> not (props.hidden |> Set.contains (n.nft.token_address, n.nft.token_id))) |> Array.length
                    if n > 0 then Some (c.collection, i, c.nfts.Length) else None)
            let firstColIndex = collections |> Array.tryHead |> Option.map (fun (_, i, _) -> i)
            div [ClassName "nft-gallery"]
                [match nftDetails.current with
                 | None -> ()
                 | Some (nft, collection) ->
                    yield NftDetailsModal { singlePageMode = false; nft = nft.nft; collection = collection; likeCount = nft.likeCount; onClose = onModalClose }
                 match firstColIndex, props.mode with
                 | Some firstColIndex, _ when displayFrom.current <= firstColIndex ->
                    yield section (mdiIcon "dots-grid") "NFT Collections"
                    yield NftCollectionBadges { collections = collections; goToCollection = goToCollection }
                    yield section (mdiIcon "dots-grid") "NFT Gallery"
                 | Some _, _ ->
                    yield section (button [ClassName "button mode-aware-border mode-aware-button"; OnClick (fun _ -> goToCollection 0)] [mdiIcon "arrow-up-thick"; str "Back to Top"]) ""
                 | None, View ->
                    ()
                 | None, Edit ->
                    yield
                        // no nfts prompt
                        div [ClassName "no-nfts"]
                            [p [ClassName "mode-aware-text"] [mdiIcon "image-frame"]
                             p [] [str "Your NFT gallery is currently empty."]
                             p [] [str "Transfer some to your account and they will automatically display below. *"]
                             p [ClassName "small-text mode-aware-text"] [str "* If they are contained in our whitelist."]]
                 yield div [ClassName "gallery"] 
                    (nfts.results
                    |> Array.mapi (fun i c ->
                        let displayCount = c.nfts |> Array.filter (fun n -> n.index < displayUntil && not (props.hidden |> Set.contains (n.nft.token_address, n.nft.token_id))) |> Array.length
                        if i >= displayFrom.current && displayCount > 0 then
                            NftGalleryCollection
                                { collection = c
                                  scrollToRef = if i = displayFrom.current then Some firstColRef else None
                                  displayUntil = displayUntil
                                  onToggleVisible = props.onToggleVisible
                                  profileNft = props.profileNft
                                  disabled = props.disabled
                                  onNftClick = onNftClick
                                  onMakeProfile =
                                    fun (a, i) ->
                                        let allnfts = nfts.results |> Array.collect (fun n -> n.nfts)
                                        let nft = allnfts |> Array.find (fun n -> n.nft.token_address = a && n.nft.token_id = i)
                                        props.onMakeProfile nft.nft
                                  mode = props.mode
                                  hidden = props.hidden
                                  videoPlaying = videoPlaying.current
                                  onEnded = fun (a, i) -> match videoPlaying.current with | Some (a1, i1, _) when a = a1 && i = i1 -> videoPlaying.update None | _ -> ()
                                  onPlay = fun (a, i) -> match videoPlaying.current with | Some (a1, i1, _) when a = a1 && i = i1 -> videoPlaying.update (Some (a, i, true)) | _ -> ()
                                  playVideo = fun (a, i) -> videoPlaying.update (Some (a, i, false)) }
                        else ofOption None) |> Array.toList)]
    ), "NftGallery", equalsButFunctions)

type EditProfileProps =
    { treeAccount: string
      ens: string option
      url: string option
      dispatch: Msg -> unit
      txDispatch: TransactionMsg -> unit
      txResult: DappTxResult<TransactionMsg> option }

let EditProfile =
    FunctionComponent.Of ((fun (props :EditProfileProps) ->
        let displayingFirstCol = Hooks.useState true
        let web3 = useWeb3React()
        let tree = Feliz.React.useDeferred (Server.getTreeAndProfileNft props.treeAccount, [||])
        let accountProfileInfo =
            Feliz.React.useDeferred(
                async {
                    if web3.active then
                        let! profileInfo = Server.getProfileInfo web3.account.Value
                        return (Some profileInfo)
                    else
                        return None
                }, [| web3.active |])
        let accountProfileInfo =
            match accountProfileInfo with
            | Deferred.Resolved (Some (Some profileInfo)) -> Some profileInfo
            | _ -> None 
        let name = Hooks.useState None
        let bio = Hooks.useState None
        let links, linksUpdater = Feliz.React.useStateWithUpdater None
        let colors = Hooks.useState None
        let darkMode = Hooks.useState None
        let hiddenNfts, hiddenNftsUpdater = Feliz.React.useStateWithUpdater None
        let profileNftState = Hooks.useState None
        // set dark mode class
        Hooks.useEffect (fun () ->
            match darkMode.current with
            | Some true ->
                document.getElementsByTagName("body").[0].classList?add("dark-mode")
            | Some false ->
                document.getElementsByTagName("body").[0].classList?remove("dark-mode")
            | None ->
                match tree with
                | Deferred.Resolved ({ darkMode = true }, _) ->
                    document.getElementsByTagName("body").[0].classList?add("dark-mode")
                | Deferred.Resolved ({ darkMode = false }, _) ->
                    document.getElementsByTagName("body").[0].classList?remove("dark-mode")
                | _ -> ())
        let editState =
            match tree with
            | Deferred.Resolved (tree, profileNft) ->
                { name = match name.current with | None -> tree.name | Some name -> name
                  bio = match bio.current with | None -> tree.bio | Some bio -> bio
                  links = match links with | None -> tree.links |> Array.mapi (fun i l -> { name = l.name; url = l.url; id = System.Guid.NewGuid().ToString(); index = i; isNew = false }) | Some links -> links
                  colors = match colors.current with | None -> tree.colors | Some colors -> colors
                  darkMode = match darkMode.current with | None -> tree.darkMode | Some darkMode -> darkMode
                  profileNft = match profileNftState.current with | None -> profileNft | Some profileNft -> profileNft
                  hidden = match hiddenNfts with | None -> tree.nfts |> Map.toSeq |> Seq.map fst |> Set.ofSeq | Some hidden -> hidden } |> Some
            | _ -> None
        let linksUpdater update =
            linksUpdater (fun oldLinks ->
                match tree with
                | Deferred.Resolved (tree, _) ->
                    let oldLinks = match oldLinks with | None -> tree.links |> Array.mapi (fun i l -> { name = l.name; url = l.url; id = System.Guid.NewGuid().ToString(); index = i; isNew = false }) | Some links -> links
                    Some (update oldLinks)
                | _ -> oldLinks)
        let linkAccept (id, name, url) =
            linksUpdater (fun oldLinks -> oldLinks |> Array.map (fun l -> if l.id = id then { l with name = name; url = url; isNew = false } else l))
        let linkUp id =
            linksUpdater (fun oldLinks ->
                let index = oldLinks |> Array.findIndex (fun l -> l.id = id)
                let a = oldLinks.[index - 1]
                let b = oldLinks.[index]
                let newLinks = oldLinks |> Array.mapi (fun i l ->
                    match i with
                    | _ when i = index - 1 -> { b with index = index - 1 }
                    | _ when i = index -> { a with index = index }
                    | _ -> l)
                newLinks)
        let linkDown id =
            linksUpdater (fun oldLinks ->
                let index = oldLinks |> Array.findIndex (fun l -> l.id = id)
                let a = oldLinks.[index + 1]
                let b = oldLinks.[index]
                let newLinks = oldLinks |> Array.mapi (fun i l ->
                    match i with
                    | _ when i = index + 1 -> { b with index = index + 1 }
                    | _ when i = index -> { a with index = index }
                    | _ -> l)
                newLinks)
        let linkDelete id = linksUpdater (fun oldLinks -> oldLinks |> Array.filter (fun l -> l.id <> id))
        let nameChange value =
            name.update (Some value)
        let bioChange value = bio.update (Some value)
        let saveDisabled =
            match name.current, bio.current, links, colors.current, darkMode.current, profileNftState.current with
            | None, None, None, None, None, None -> true
            | _ -> false
        let linkNew () =
            linksUpdater (fun oldLinks -> Array.append oldLinks [| { name = ""; url = ""; isNew = true; index = oldLinks.Length; id = System.Guid.NewGuid().ToString() } |])
        let selectColor cs = colors.update (Some cs)
        let selectDarkMode dm =
            darkMode.update (Some dm)
        let isLight, isDark =
            match editState with
            | Some state ->
                state.colors |> ColorUtils.isLight, state.colors |> ColorUtils.isDark
            | _ -> false, false
        let toggleVisible nft =
            match tree with
            | Deferred.Resolved (tree, _) ->
                hiddenNftsUpdater (fun oldHidden ->
                    let set =
                        match oldHidden with
                        | None -> tree.nfts |> Map.toSeq |> Seq.map fst |> Set.ofSeq
                        | Some set -> set
                    Some (if set |> Set.contains nft then set |> Set.remove nft else set |> Set.add nft))
            | _ -> ()
        let makeProfile nft = profileNftState.update (Some (Some nft))
        // web3 TXs
        let save () =
            match editState with
            | Some state ->
                let nfts =
                    match tree with
                    | Deferred.Resolved(tree, _ ) ->
                        match hiddenNfts with
                        | None -> tree.nfts
                        | Some hidden -> hidden |> Set.toSeq |> Seq.map (fun n -> n, true) |> Map.ofSeq
                let tree =
                    { profileNft = state.profileNft |> Option.map (fun n -> n.token_address, n.token_id)
                      name = state.name
                      bio = state.bio
                      links = state.links |> Array.map (fun l -> { name = l.name; url = l.url })
                      nfts = nfts
                      colors = state.colors
                      darkMode = state.darkMode }
                let text = TreeData.toMarkdown tree
                props.txDispatch (UpdateProfile text)
            | _ -> ()
        let saveCallback = Feliz.React.useCallbackRef(save)
        div [ClassName "profile edit has-bar"] 
            [ofOption (props.txResult |> Option.map (fun txResult -> DialogModal { active = true; txResult = txResult; accountProfileInfo = accountProfileInfo; account = props.treeAccount; ens = props.ens; url = props.url }))
             EditBar { saveDisabled = saveDisabled; treeAccount = props.treeAccount; treeChainId = 42; onSave = saveCallback }
             LinkTreeEdit
                  { treeAccount = props.treeAccount
                    hide = not displayingFirstCol.current
                    isColorLight = isLight
                    isColorDark = isDark
                    disabled = web3.account <> (Some props.treeAccount)
                    ens = props.ens
                    state = editState
                    onLinkAccept = linkAccept
                    onLinkUp = linkUp
                    onLinkDown = linkDown
                    onLinkDelete = linkDelete
                    onNameChange = nameChange
                    onBioChange = bioChange
                    onLinkNew = linkNew
                    onSelectColor = selectColor
                    onSelectDarkMode = selectDarkMode }
             match editState with
             | Some state ->
                   NftGallery
                    { nftAccount = props.treeAccount
                      onGoToCollection = fun (f, i) -> displayingFirstCol.update ((f = i))
                      hidden = state.hidden
                      profileNft = state.profileNft |> Option.map (fun n -> n.token_address, n.token_id)
                      mode = Edit
                      disabled = web3.account <> (Some props.treeAccount)
                      onToggleVisible = toggleVisible
                      onMakeProfile = makeProfile }
             | _ -> ofOption None]
    ), "EditProfile", equalsButFunctions)

