type ViewProfileProps = { treeAccount: string; ens: string option }

let ViewProfile =
    FunctionComponent.Of ((fun (props :ViewProfileProps) ->
        // hooks
        let tree = Feliz.React.useDeferred (Server.getTreeAndProfileNft props.treeAccount, [| |])
        let displayingFirstCol = Hooks.useState true
        Hooks.useEffect (fun () ->
            match tree with
            | Deferred.Resolved ({ darkMode = true }, _) ->
                document.getElementsByTagName("body").[0].classList?add("dark-mode")
            | Deferred.Resolved ({ darkMode = false }, _) ->
                document.getElementsByTagName("body").[0].classList?remove("dark-mode")
            | _ -> ())
        match tree with
        | Deferred.Resolved (tree, profileNft) ->
            let hidden = tree.nfts |> Map.toArray |> Array.choose (fun (n, h) -> if h then Some n else None) |> Set.ofArray
            div [ClassName "profile view"]
                [LinkTreeView { treeAccount = props.treeAccount; ens = props.ens; tree = tree; profileNft = profileNft; hide = not displayingFirstCol.current }
                 NftGallery { disabled = false; nftAccount = props.treeAccount; hidden = hidden; mode = View; onGoToCollection = (fun (f, i) -> displayingFirstCol.update((f = i))); onToggleVisible = (fun _ -> ()); onMakeProfile = (fun _ -> ()); profileNft = profileNft |> Option.map (fun nft -> nft.token_id, nft.token_id) }]
        | _ -> bigLoading
    ), "ViewProfile", equalsButFunctions)

let root (model :Model) (txResult :DappTxResult<TransactionMsg> option) dispatch txDispatch =
    match model.address, model.ens, model.url with
    | Some (Some address), Some ens, Some url ->
        let content =
            match model.mode with
            | View ->
                ViewProfile { treeAccount = address; ens = ens; }
            | Edit ->
                EditProfile { treeAccount = address; ens = ens; url = url; dispatch = dispatch; txDispatch = txDispatch; txResult = txResult }
        content
    | Some (None), _, _ ->
        div [ClassName "not-found"] [
            h1 [] [str "Page Not Found"]
            p [] [str "Sorry, this page is not available"]]
    | _ -> bigLoading
