module App.Types

open Global
open Imports
open Common

type Msg =
    | HomeMsg of Home.Types.Msg
    | NewMsg of New.Types.Msg
    | TreeMsg of Tree.Types.Msg
    | SetProvider of obj
    | SetAccountData of AccountData
    | ChangePage of Page
    | RefreshAccounts
    | SetProfileInfo of ProfileInfo option
    | ScrollToBottom

type GlobalObjs =
    { web3Modal: obj//Web3Modal
      window: obj
      mutable web3: obj//Web3
      mutable web3treeContract :obj }

type Model =
    { page: Page
      home: Home.Types.Model
      tree: Tree.Types.Model
      common: CommonModel }
