module Home.View

open Fable.Core
open Fable.Core.JsInterop
open Fable.React
open Fable.React.Props
open Types
open Common
open Global
open System.Globalization
open Imports
open Utils

let HomePage =
    FunctionComponent.Of ((fun (props : {| dispatch: Msg -> unit |}) ->
        let web3 = useWeb3React()
        div [ClassName "home"]
            [img [Src "/img/tree.svg"]
             h1 [] [str "Web3Tree"]
             match web3.active with
             | false ->
                yield!
                    [p [] [str "Connect your account to administer your Web3 link bio page"]
                     button [OnClick (fun _ -> web3.activate(Injected)); ClassName "button"] [str "Connect"]]
             | true ->
                yield!
                    [jazziconForAddress 48 web3.account.Value
                     p [] [b [] [str (niceAddress web3.account.Value)]]
                     button [OnClick (fun _ -> props.dispatch (ChangePage (View web3.account.Value))); ClassName "button"] [str "View Page"]
                     button [OnClick (fun _ -> props.dispatch (ChangePage (Edit web3.account.Value))); ClassName "button"] [str "Edit Page"]]
             ]
   ), "HomePage", equalsButFunctions)

let connectBtn dispatch = div [] [button [ClassName "button home-button"; OnClick <| fun _ -> dispatch (ChangePage (Edit "0xC1A32Cb082ae11A9b4562A96Ab43ff3C1e174F6D"))] [str "Connect Account"]]

let root model common dispatch = HomePage {| dispatch = dispatch |}
