let pageParser: Parser<Page->Page,Page> =
    let nftDetails a i = NftDetails (a, i)
    oneOf [
        map Home (s "")
        map Edit (s "edit" </> str)
        map nftDetails (s "nft" </> str </> str)
        map View (str)
    ]

let urlUpdate gbl (result : Page option) model =
    let setClasses className =
        (document.getElementById "html").className <- className
        (document.getElementById "body").className <- className
    match result with
    | None ->
        console.error("Error parsing url")
        model, Navigation.modifyUrl (toUrl model.page)
    | Some Home ->
        setClasses "page-home"
        { model with page = Home }, Cmd.none
    | Some (NftDetails (address, id) as page) -> failwith "ni"
        //match model with
        //| { page = View _; tree = { nftDetails = Some nftDetails } } when nftDetails.nft.token_address = address && nftDetails.nft.token_id = id ->
        //    model , Cmd.none
        //| _ ->
        //    setClasses "page-view"
            
        //    { model with page = page }, Cmd.OfAsync.perform (fun (address, id) ->
        //        async {
        //            match! Server.getNFTDetails address id with
        //            | None -> return failwith "nft not found"
        //            | Some nft ->
        //                match! Server.getCollection address with
        //                | None -> return failwith "collection not found"
        //                | Some collection ->
        //                    match! Server.getNFTOwners address id with
        //                    | Some { owners = [| owner |] } ->
        //                        let! tree = Server.getTreeData owner
        //                        return nft, collection, owner, Some tree
        //                    | _ -> return nft, collection, "", None
        //        }) (address, id) (NewMsg << New.Types.Msg.SetNftDetailsForPage)
    | Some ((View user) as page) | Some ((Edit user) as page) ->
        match model with
        //| { page = currentPage; tree = { nftDetails = Some _ } } when currentPage = page ->
        //    (Browser.Dom.document.getElementById "body").classList.remove("rmodal-open")
        //    (Browser.Dom.document.getElementById "html").classList.remove("rmodal-open")
        //    { model with tree = { model.tree with nftDetails = None } }, Cmd.none
        | _ ->
            setClasses <|
                match page with
                | View _ -> "page-view mode-aware-gradient-back"
                | Edit _ -> "page-edit mode-aware-gradient-back"
                | _ -> ""
            // determine whether the user part of the url is an address or ens
            let mode =
                match page with
                | View _ -> Tree.Types.View | Edit _ -> Tree.Types.Edit
            if user.StartsWith("0x") && user.Length = 42 then
                // address
                { model with page = page; tree = { model.tree with address = Some (Some user); mode = mode } }, 
                    Cmd.batch
                        [Cmd.OfAsync.perform Server.getEns user (TreeMsg << Tree.Types.Msg.SetEns)
                         Cmd.OfAsync.perform Server.getUrlForAddress user (TreeMsg << Tree.Types.Msg.SetUrl)]
            else if user.StartsWith("-") then
                // url
                { model with page = page; tree = { model.tree with url = Some (Some user); mode = mode } }, 
                    Cmd.batch
                        [Cmd.OfAsync.perform Server.getEns user (TreeMsg << Tree.Types.Msg.SetEns)
                         Cmd.OfAsync.perform Server.getAddressForUrl user (TreeMsg << Tree.Types.Msg.SetAddress)]
            else
                // ens
                { model with page = page; tree = { model.tree with ens = Some (Some user); mode = mode; url = Some None } },
                    Cmd.OfAsync.perform Server.getAddressForEns user (TreeMsg << Tree.Types.Msg.SetAddress)

let init gbl result =
    let (home, homeCmd) = Home.State.init()
    let (model, cmd) =
        urlUpdate gbl result
          { page = Home
            home = home
            tree =
                { ens = None
                  address = None
                  url = None
                  mode = Tree.Types.View }
            common =
                { accountData = None } }
    model, Cmd.batch [ cmd
                       Cmd.map HomeMsg homeCmd ]

